/* room css */

body {
  font-family: "PT Serif", serif;
  font-family: "Philosopher", sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f9fa;
}

h1 h2 h3 h4 h5 h6 {
  font-family: "PT Serif", serif;
}
p {
  font-family: "Philosopher", sans-serif;
}
a {
  font-family: "PT Serif", serif;
}

/* small scree css start */

@media (max-width: 700px) {
  .dining-banner-img {
    width: 100%;
  }
