    justify-content: center;
    align-items: center;
  }

  .ameneties-title h2 {
    margin-bottom: 0;
  }
  .amenetiesflex {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 3%;
  }

  .amenetiesone {
    width: 30%;
